.diary-layout {
  display: flex;
  flex-direction: column;
  height: 100vh;
  background: linear-gradient(135deg, #f5f7fa 0%, #c3cfe2 100%);

  :host-context(.dark-theme) & {
    background: linear-gradient(135deg, #1a202c 0%, #2d3748 100%);
  }
}

.diary-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 16px 24px;
  background: linear-gradient(135deg, #ff6b35 0%, #ff8c42 100%); // 라이트모드: 주황색
  box-shadow: 0 2px 8px rgba(255, 107, 53, 0.3);
  z-index: 100;

  :host-context(.dark-theme) & {
    background: linear-gradient(135deg, #10b981 0%, #059669 100%); // 다크모드: 녹색
    box-shadow: 0 2px 8px rgba(16, 185, 129, 0.3);
  }
}

.header-left,
.header-right {
  display: flex;
  align-items: center;
  gap: 16px;
}

.header-title {
  margin: 0;
  font-size: 1.5rem;
  font-weight: 700;
  color: white; // 라이트모드: 흰색
  text-shadow: 0 2px 4px rgba(0, 0, 0, 0.2);

  :host-context(.dark-theme) & {
    color: white; // 다크모드: 흰색
    text-shadow: 0 2px 4px rgba(0, 0, 0, 0.3);
  }
}

.icon-button {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 40px;
  height: 40px;
  border: none;
  border-radius: 50%;
  background: transparent;
  cursor: pointer;
  transition: all 0.2s ease;

  .material-icons {
    font-size: 24px;
    color: white; // 라이트모드: 흰색

    :host-context(.dark-theme) & {
      color: white; // 다크모드: 흰색
    }
  }

  &:hover {
    background: rgba(255, 255, 255, 0.2); // 라이트모드: 흰색 오버레이

    .material-icons {
      color: white;

      :host-context(.dark-theme) & {
        color: white;
      }
    }

    :host-context(.dark-theme) & {
      background: rgba(255, 255, 255, 0.15); // 다크모드: 약간 더 투명한 흰색
    }
  }

  &:active {
    transform: scale(0.95);
  }
}

.user-info {
  font-size: 0.9rem;
  color: white; // 라이트모드: 흰색
  font-weight: 500;
  text-shadow: 0 1px 2px rgba(0, 0, 0, 0.2);

  :host-context(.dark-theme) & {
    color: white; // 다크모드: 흰색
    text-shadow: 0 1px 2px rgba(0, 0, 0, 0.3);
  }
}

.diary-main {
  flex: 1;
  overflow: auto;
  padding: 24px;

  // 스크롤바 스타일
  &::-webkit-scrollbar {
    width: 8px;
  }

  &::-webkit-scrollbar-track {
    background: rgba(0, 0, 0, 0.05);
  }

  &::-webkit-scrollbar-thumb {
    background: rgba(102, 126, 234, 0.3);
    border-radius: 4px;

    &:hover {
      background: rgba(102, 126, 234, 0.5);
    }
  }

  :host-context(.dark-theme) & {
    &::-webkit-scrollbar-track {
      background: rgba(255, 255, 255, 0.05);
    }

    &::-webkit-scrollbar-thumb {
      background: rgba(165, 180, 252, 0.3);

      &:hover {
        background: rgba(165, 180, 252, 0.5);
      }
    }
  }
}

// 사이드 메뉴 오버레이
.side-menu-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  z-index: 200;
  opacity: 0;
  pointer-events: none;
  transition: opacity 0.3s ease;

  &.active {
    opacity: 1;
    pointer-events: all;
  }
}

// 사이드 메뉴
.side-menu {
  position: fixed;
  top: 0;
  left: -320px;
  width: 320px;
  height: 100%;
  background: white;
  box-shadow: 2px 0 8px rgba(0, 0, 0, 0.1);
  z-index: 201;
  transition: left 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  overflow-y: auto;

  :host-context(.dark-theme) & {
    background: #2d3748;
    box-shadow: 2px 0 8px rgba(0, 0, 0, 0.3);
  }

  &.active {
    left: 0;
  }

  // 스크롤바 스타일
  &::-webkit-scrollbar {
    width: 6px;
  }

  &::-webkit-scrollbar-track {
    background: rgba(0, 0, 0, 0.05);
  }

  &::-webkit-scrollbar-thumb {
    background: rgba(255, 107, 53, 0.3);
    border-radius: 3px;

    &:hover {
      background: rgba(255, 107, 53, 0.5);
    }
  }

  :host-context(.dark-theme) & {
    &::-webkit-scrollbar-thumb {
      background: rgba(16, 185, 129, 0.3);

      &:hover {
        background: rgba(16, 185, 129, 0.5);
      }
    }
  }
}

.side-menu-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  background: linear-gradient(135deg, #ff6b35 0%, #ff8c42 100%);
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);

  :host-context(.dark-theme) & {
    background: linear-gradient(135deg, #10b981 0%, #059669 100%);
  }

  .side-menu-title {
    margin: 0;
    font-size: 1.3rem;
    font-weight: 700;
    color: white;
  }

  .close-button {
    .material-icons {
      color: white;
    }
  }
}

.side-menu-nav {
  padding: 20px 0;
}

.menu-section {
  margin-bottom: 24px;
  padding: 0 20px;

  &:last-child {
    margin-bottom: 0;
  }
}

.menu-section-title {
  margin: 0 0 12px 0;
  font-size: 0.75rem;
  font-weight: 600;
  color: #9ca3af;
  text-transform: uppercase;
  letter-spacing: 0.05em;

  :host-context(.dark-theme) & {
    color: #6b7280;
  }
}

.user-profile {
  display: flex;
  align-items: center;
  gap: 12px;
  padding: 12px;
  background: rgba(255, 107, 53, 0.1);
  border-radius: 8px;
  margin-bottom: 8px;

  :host-context(.dark-theme) & {
    background: rgba(16, 185, 129, 0.1);
  }

  .user-avatar {
    font-size: 40px;
    color: #ff6b35;

    :host-context(.dark-theme) & {
      color: #10b981;
    }
  }

  .user-details {
    flex: 1;
  }

  .user-name {
    margin: 0 0 4px 0;
    font-size: 0.95rem;
    font-weight: 600;
    color: #1f2937;

    :host-context(.dark-theme) & {
      color: #f9fafb;
    }
  }

  .user-login-time {
    margin: 0;
    font-size: 0.75rem;
    color: #6b7280;

    :host-context(.dark-theme) & {
      color: #9ca3af;
    }
  }
}

.menu-item {
  display: flex;
  align-items: center;
  gap: 12px;
  width: 100%;
  padding: 12px 16px;
  margin-bottom: 4px;
  border: none;
  background: transparent;
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.2s ease;
  text-align: left;
  font-size: 0.95rem;
  color: #4b5563;

  :host-context(.dark-theme) & {
    color: #d1d5db;
  }

  .material-icons {
    font-size: 20px;
    color: #6b7280;

    :host-context(.dark-theme) & {
      color: #9ca3af;
    }
  }

  &:hover {
    background: rgba(255, 107, 53, 0.1);
    color: #ff6b35;

    .material-icons {
      color: #ff6b35;
    }

    :host-context(.dark-theme) & {
      background: rgba(16, 185, 129, 0.1);
      color: #10b981;

      .material-icons {
        color: #10b981;
      }
    }
  }

  &.logout-menu-item {
    color: #dc2626;

    .material-icons {
      color: #dc2626;
    }

    &:hover {
      background: rgba(220, 38, 38, 0.1);
      color: #dc2626;

      .material-icons {
        color: #dc2626;
      }

      :host-context(.dark-theme) & {
        background: rgba(220, 38, 38, 0.1);
      }
    }
  }
}

// 반응형
@media (max-width: 768px) {
  .diary-header {
    padding: 12px 16px;
  }

  .header-title {
    font-size: 1.2rem;
  }

  .user-info {
    display: none; // 모바일에서는 사용자 이름 숨김
  }

  .diary-main {
    padding: 16px;
  }
}

@media (max-width: 480px) {
  .header-left,
  .header-right {
    gap: 8px;
  }

  .icon-button {
    width: 36px;
    height: 36px;

    .material-icons {
      font-size: 20px;
    }
  }

  .side-menu {
    width: 280px;
    left: -280px;
  }
}
